    },
    props:[
      'isDelete'
    ],
    inject:['reload'],//
    data () {
      return {
        page:"article",
        articleList:[],
        key:'',
        totalPage:1,
        currentPage:1,
        // testList:[],
        classifys:{}
      }
    },
    methods: {
      //
