        console.log(key, keyPath);
      },
      //
      currentChange(val){
        this.currentPage = val;
        this.getArticles();
      },
      getArticles(){
        let that = this;
        //
        this.$http.get('/api/article/search',{
          params:{
            key:that.key,
            classify:"",
            page:that.currentPage,
            status:0,
          }
        }).then(function(res){
            that.articleList = res.data.articles;
            that.totalPage = res.data.totalPage
            console.log(" "+that.articleList.length);
          }).catch(function(){
          console.log("");
        });
      }
    },
    created() {
