          // console.log(" "+JSON.stringify(res));
          that.msg = res.data;
        }).catch(function(){
        console.log("");
      });
      this.getArticles();
      bus.$on('search', res=>{
        this.key = res
        that.getArticles()
      })
    },
    mounted() {
      //
